import { ThreeCore } from './ThreeCore';
import { Core } from './Core/Core';
export class AppThreeGlobeNetwork {
    threeCore: ThreeCore;
    core: Core;

    constructor(id: string = 'SandBox') {
        this.threeCore = new ThreeCore(id);
        this.core = new Core(this.threeCore);
        this.BindEvent();
        this.BindRenderer();
    }
    BindEvent() {
        this.threeCore.MouseDown = (e: MouseEvent) => this.core.MouseDown(e);
        this.threeCore.MouseMove = (e: MouseEvent) => this.core.MouseMove(e);
        this.threeCore.MouseUp = (e: MouseEvent) => this.core.MouseUp(e);
    }
    BindRenderer() {
        this.threeCore.Render = () => this.core.Render();
    }
}

export class Solution {
    public divHost: HTMLElement;

    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement;

        new AppThreeGlobeNetwork(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}